@import url(https://fonts.googleapis.com/css?family=Nosifer&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

/* contains background image primarily */

body {
  background-image: url(/static/media/background-image-4.f9aca5a1.jpg);
  background-repeat: no-repeat;
  background-size: 1500px 900px;
  background-color: #7467ad;
  margin-left: 0;
  text-align: center;
}

/* this is the primary container for nearly all content with exception to the footer and image-2 */

.container {
  background-color: #070037;
  border: solid #dadada;
  border-width: 7px 7px 7px 7px;
  border-radius: 42px;
  width: 60vw;
  /* height: 750px; */
  height: 82%;
  margin-top: 20px;
  margin-bottom: 5%;
  margin-left: 18vw;
  /* padding-bottom: 1.5vw; */
  -webkit-align-content: center;
          align-content: center;
  position: absolute;
  /* z-index: 1; */
}

/* front page image of monster */

@media only screen and (min-width: 200px) {
  #image-display {
      border-radius: 42px;
      /* width: 25vw; */
      /* height: 25vw; */
      margin-top: -1vw;
      width: 42%;
      height: 42%;
      /* width: 350px;
              height: 350px; */
      min-width: 300px;
      min-height: 300px;
  }
}

/* angled image (of monster) */

/* this image default is hidden */

/* image that appears on page 2 (not attached to any other element on the page) */

#image-display-2:hover {
  margin-left: -50vw;
}

/* Chat bubbles should have delay after monster (image-display-2) toggles in */

/* each bubble will appear in order (1...2...3) */


/* add eventlistener("click" for image-display-2) */

#nomster-text {
  margin-top: 3vw;
  color: #77dfff;
  /* font-size: 25px; */
  font-size: 1.6vw;
  font-weight: bold;
  text-align: center;
}

/* Page 2 search results */

.search-results {
  background-color: #7467ad;
  color: rgb(7, 0, 55);
  font-size: 20px;
  font-weight: bold;
  width: 40%;
  /* margin-top: 20px;
                  margin-bottom: 20px;
                  margin-left: 20px;
                  */
  margin-top: 2%;
  margin-bottom: 2%;
  margin-left: 2%;
  /* height: 45.7vw; */
  height: 93%;
  overflow: auto;
  border: solid #77dfff;
  border-width: 5px 5px 5px 5px;
  /* border-top-left-radius: 42px;
                  border-top-right-radius: 42px; */
  border-radius: 42px;
  float: left;
}

#noms {
  margin-top: 0;
  margin-bottom: 0;
  padding-top: 10px;
  padding-bottom: 10px;
  background-color: rgb(7, 0, 55);
  font-size: 2vw;
  /* -webkit-text-stroke: 1px #77dfff; */
  /* color: rgb(7, 0, 55); */
  color: rgb(255, 0, 42);
  -webkit-text-stroke: .5px rgb(142, 0, 0);
  font-weight: bold;
  border-bottom: solid #77dfff;
  /* border: solid #77dfff; */
  border-width: 5px 5px 5px 5px;
  font-family: 'Nosifer', cursive;
}

/* this div houses the individual business divs that the list generates */

.businesses {
  margin-top: 0;
  margin-bottom: 0;
  padding: 0;
}

/* these are generated by the restaurants.js file */

.business {
  padding-top: 10px;
  padding-bottom: 10px;
  border-bottom: solid rgb(7, 0, 55);
  background-color: #7467ad;
  color: white;
  font-size: 12px;
  border-width: 2px 2px 2px 2px;
}

.name {
  font-size: 17px;
  font-weight: bold;
}

.search-results::-webkit-scrollbar-track {
  border: 1px solid #77dfff;
  padding: 5px;
  background-color: rgb(7, 0, 55);
  border-radius: 42px;
  position: absolute;
  margin-bottom: 25px;
}

.search-results::-webkit-scrollbar {
  width: 25px;
  border-radius: 42px;
}

.search-results::-webkit-scrollbar-thumb {
  border: 5px solid #77dfff;
  border-radius: 42px;
  height: 25px;
  width: 25px;
  /* box-shadow: inset 0 0 6px rgb(255, 0, 0); */
  background-color: rgb(7, 0, 55);
}

/* page 2 map for search results */

.results-map {
  background-color: rgb(7, 0, 55);
  color: #77dfff;
  font-size: 20px;
  font-weight: bold;
  width: 45%;
  /* margin-top: 20px;
                  margin-bottom: 20px;
                  margin-right: 20px; */
  margin-top: 2%;
  margin-bottom: 2%;
  margin-right: 2%;
  /* height: 45.7vw; */
  height: 93%;
  overflow: hidden;
  border: solid #77dfff;
  border-width: 5px 5px 5px 5px;
  border-radius: 42px;
  float: right;
}

#path {
  margin-top: 0;
  padding-top: 10px;
  padding-bottom: 10px;
  background-color: rgb(7, 0, 55);
  font-size: 2vw;
  /* -webkit-text-stroke: 1px #77dfff; */
  /* color: rgb(7, 0, 55); */
  color: rgb(255, 0, 42);
  -webkit-text-stroke: .5px rgb(142, 0, 0);
  font-weight: bold;
  border-bottom: solid #77dfff;
  border-width: 5px 5px 5px 5px;
  font-family: 'Nosifer', cursive;
}

/* home-tab default is display: none */

/* becomes visible with page 2 */

#home-tab {
  display: none;
}

#myMap {
  width: 100%;
  /* height: 630px; */
  height: 100%;
  margin-top: -2vw;
  /* margin-top: 20px; */
  /* margin-bottom: 5%; */
  /* margin-left: 18vw; */
  /* padding-bottom: 1.5vw; */
}



/* default is hidden */

#about-us {
  position: absolute;
  margin-top: 15%;
  margin-left: 21%;
  padding: 30px;
  /* width: 9vw; */
  /* height: 9vw; */
  width: 50%;
  height: 50%;
  background-color: rgb(7, 0, 55);
  color: #77dfff;
  /* font-size: 1.15vw; */
  font-size: 1.15vmax;
  border: solid #dadada;
  border-width: 2px;
  border-radius: 70px;
  z-index: 1;
  display: none;
}

/* default is hidden */

#contact-us {
  position: absolute;
  margin-top: 29%;
  margin-left: 40%;
  padding: 30px;
  /* width: 9vw; */
  /* height: 9vw; */
  width: 13%;
  height: 5%;
  background-color: rgb(7, 0, 55);
  color: #77dfff;
  /* font-size: 1.15vw; */
  font-size: 1.15vmax;
  border: solid #dadada;
  border-width: 2px;
  border-radius: 70px;
  z-index: 1;
  display: none;
}
